@import '../../../style/variables.scss';
.item {
    display: flex;
    flex-direction: column;
    align-items:  center;
    justify-content: center;
    padding: 1rem 0;
    position: relative;
    .image {
        text-align: center;
        margin-bottom: 1rem;
        img{
            width: 50%;
        }
    }
    .designer {
        text-transform: uppercase;
        font-size: 1.5rem;
        font-weight: 200;
        margin-bottom: 1rem;
    }
    .name{
        font-weight: 700;
        font-size: 1.7rem;
        color: $tamna-braon;
        margin-bottom: 1rem;
    }
    .box {
        display: flex;
        flex-direction: column;
        width: 100%;
        align-items: center;
        margin: 1rem 0;
        text-align: center;
        .changer{
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
            margin-top: 1rem;
            span {
                font-size: 1.7rem;
                font-weight: 700;
                margin: 0 .5rem;
            }
            img {
                width: 1.5rem;
            }
            img:hover {
                background-color: $roze;
                cursor: pointer;
            }
        }
    }
    .price {
        font-size: 1.5rem;
        font-weight: 600;
        color: $tamna-braon;
        display: flex;
        justify-content: space-around;
        span{
            font-size: 1.3rem;
            line-height: 1.7rem;
        }
    }
    .deleteItem{
        padding: .5rem 0;
    }
    
}
.item:hover{
    box-shadow: -1px 0px 44px -19px rgba(0,0,0,0.82);
    
    .deleteItem {
        position: absolute;
        top: 0;
        right: 0;
        width: 15%;  
        text-align: center; 
           
        img{
            cursor: pointer;
            width: 40%;
            
        }
        img:hover{
            width: 50%;
        }
    }
}